.Spotify {
  text-align: center;
  width: 60%;
}

.Spotify-header {
  background-color: #222;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Spotify-link {
  color: #61dafb;
}

.hidden {
  display: none;
}

/** Buttons **/

.btn {
  background-color: transparent;
  border-radius: 2em;
  border: 0.1em  #1ecd97;
  color: #1ecd97;
  cursor: pointer;
  font-size: 3vmin;
  padding: 0.7em 1.5em;
  text-transform: uppercase;
  transition: all 0.25s ease;
  text-decoration: none;
}

.btn:hover {
  background: #1ecd97;
  color: #333;
}

.btn--login {
  margin: 0 auto;
}

/** Background **/

.background {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-size: cover;
  background-position: center center;
  filter: blur(8em) opacity(0.6);
  position: absolute;
}

.main-wrapper {
  align-items: center;
  display: flex;
  height: 100%;
  margin: 0 auto;
  justify-content: center;
  position: relative;
  width: 80%;
  z-index: 1;
}

.container {
  align-items: left;
  display: flex;
  justify-content: center;
  height: 100%;
}

.main-container {
  flex: 1;
}

#spotify_button {
  display:inline-block;
  font-size: 25px;
  font-weight: bold;
  padding: 0;
  border: none;
  background: none;

}
#spotify_button:focus{
  outline: none;
}

#spotify_span{
  font-size: 28px;
}

.SpotifyPlayer{
  min-width: 260px;
}